h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  /* padding: 0; */
}
ul,
ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}
img {
  display: block;
}
input,
textarea {
  resize: none;
  padding: 0;
}

address {
  font-style: normal;
}
button {
  font-family: inherit;
}
a {
  text-decoration: none;
}
